.App {
	overflow: scroll;
	z-index: 999;
	height: 1000px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-body {
  font-size: large;
	/*text-align: center;*/
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.under-logo {
	height: 100px;
}

.kishore-logo-div {
    width: 100px;
    height: 100px;
    text-align: center;
    padding: 5px;
    border: 1px white solid;
    border-radius: 63px;
}

h1.kishore-logo {
    color: white;
    outline: none;
}
.Typist.text-animate {
    text-align: center;
    font-size: 25px;
    font-family: monospace;
}

@media only screen and (max-width: 900px) {
    .Typist.text-animate  {
        font-size: 14px;
    }
}

.footer {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
  background-color: #efefef;
  text-align: center;
}

.quote-home {
    margin: 85px;
    text-align: center;
    font-family: cursive;
}


a.facebook-button {
    border: none;
    background: #3b5998;
    border-radius: 6px;
    color: white;
		padding: 5px;
}
a.facebook-button:hover {
    padding: 8px;
    text-decoration: none;
}

a.Git-button.pull-right {
    color: white;
    font-size: 20px;
}



